/* ---------------------------------------Sidebar---------------------------- */

.sidebar {
    position: fixed;
    width: 80px;
    background: blue;
    display: flex;
    flex-direction: column;
    justify-content: center;
    top: 0;
    left: 0;
    height: 100%;
    align-items: center;
    color: #fff;
    padding: 70px 0;
    z-index: 2;
  }
  .sidebar svg {
    width: 30px;
    height: 50px;
    font-size: 30px;
  }
  .sidebar svg:hover {
    background: #fff;
    color: blue;
    border-radius: 8px;
    transition: 0.3s;
    cursor: pointer;
  }
  .sidebar .links {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  .wrapper {
    display: none;
  }
  .wrapper .hamburger {
    display: none;
  }
  
  @media (max-width: 1208px) {
    .sidebar {
      background: blue;
      left: -100%;
      transition: 0.3s;
    }
    .show {
      left: 0;
      transition: 0.3s;
    }
    .wrapper {
      display: block;
      position: fixed;
      top: 30px;
      left: 20px;
      font-size: 34px;
      color: black;
      height: 40px;
      width: 30px;
      border-radius: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .wrapper .hamburger {
      display: block;
      
    }
    
  }
  @media (max-width: 485px) {
    .wrapper {
      left: 20px;
    }
    .show {
      width: 80px;
    }
  }

  /* --------------------------------------Dashboard-------------------------- */

.page {
  padding: 20px;
}

.banner {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
  margin-bottom: 40px;
}

.banner > div {
  background: linear-gradient(135deg, #456ced, #120195);
  color: white;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  flex: 1;
  min-width: 250px;
  text-align: center;
  transition: transform 0.3s;
}

.banner > div:hover {
  transform: translateY(-10px);
}

.banner .firstBox {
  display: flex;
  align-items: center;
  gap: 20px;
}

.banner .firstBox img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.banner .content {
  text-align: left;
}

.banner .content p {
  margin: 0;
  font-size: 1.2rem;
}

.banner .secondBox,
.banner .thirdBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.banner .secondBox p,
.banner .thirdBox p {
  margin: 0;
  font-size: 1.2rem;
}

.banner .secondBox h3,
.banner .thirdBox h3 {
  margin-top: 10px;
  font-size: 2.5rem;
  color: white;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}
table td{border: 1px solid blue;
color:blue}

table th,
table td {
  padding: 12px;
  text-align: left;
  word-wrap: break-word; 
}

table th {
  background-color: blue;
  border: 1px solid #ddd;
  color: white;
}

table td select {
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.value-pending {
  background-color: #f0ad4e;
  color: white;
}

.value-accepted {
  background-color: #5cb85c;
  color: white;
}

.value-rejected {
  background-color: #d9534f;
  color: white;
}

.green {
  color: #5cb85c;
}

.red {
  color: #d9534f;
}

@media (max-width: 768px) {
  .banner {
    flex-direction: column;
    align-items: center;
  }

  .banner .firstBox {
    flex-direction: column;
    align-items: center;
  }

  .banner .content {
    text-align: center;
  }

  table {
    font-size: 0.9rem;
  }

  table th, table td {
    padding: 8px;
  }

  .banner > div {
    width: 100%;
  }
}

@media (max-width: 480px) {
  table, th, td {
    display: block;
  }

  table th {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  table td {
    position: relative;
    padding-left: 50%;
    padding-top: 20%;
    text-align: left;
  }

  table td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    content: attr(data-label);
    font-weight: bold;
  }

  .banner .firstBox img {
    width: 80px;
    height: 80px;
  }

  .banner .secondBox h3,
  .banner .thirdBox h3 {
    font-size: 1.5rem;
  }
}

