  /* Message-Section---------------------------------------------------- */


.page.messages {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
}

.page.messages h2 {
  font-size: 2.5rem;
  margin-bottom: 30px;
  color: blue; 
  text-align: center;
}

.banner5 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.msg-card {
 
  background-image: url('./messagebg2.avif'); 
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
  padding: 20px;
  width: 300px;
  transition: transform 0.3s ease;
  position: relative;
}

.msg-card:hover {
  transform: translateY(-5px); 
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); 
}

.msg-card .details {
  margin-bottom: 20px;
}

.msg-card .details p {
  margin-bottom: 10px;
  color: #555555; 
}

.msg-card button {
  background-color: transparent; 
  border: none;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  transition: transform 0.3s ease;
}

.msg-card button:hover {
  transform: scale(1.1); 
}

.msg-card button .MuiSvgIcon-root {
  font-size: 32px;
  color: #e53935; 
}

@media (max-width: 768px) {
  .msg-card {
    width: calc(100% - 40px); 
  }
}

  
/*Add-New-Doctor-Section--- ---------------------------------------------------- */


.page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f2f4f8; 
}

.containerd.add-doctor-form {
  background-color: #4d9aff;
  background-image: url('./bg.avif'); 
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 40px;
  border-radius: 10px;
  box-shadow:   0 4px 12px rgb(37, 51, 62);
  width: 100%;
  max-width: 600px;
}

.containerd.add-doctor-form .logo {
  width: 100px;
  height: auto;
  margin-bottom: 20px;
}

.containerd.add-doctor-form .form-title {
  font-size: 2rem;
  color: blue; 
  margin-bottom: 20px;
  text-shadow: 2px 2px 2px rgb(46, 162, 245);
}

.containerd.add-doctor-form form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.containerd.add-doctor-form form .first-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.containerd.add-doctor-form form .first-wrapper img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  box-shadow:   0 4px 12px rgb(46, 162, 245);
}

.containerd.add-doctor-form form .first-wrapper input,
.containerd.add-doctor-form form .first-wrapper select {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  background-color: #e9ebeb;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: border-color 0.3s ease;
  margin: 4px;
}

.containerd.add-doctor-form form .first-wrapper input:focus,
.containerd.add-doctor-form form .first-wrapper select:focus {
  outline: none;
  border-color: blue;
}

.containerd.add-doctor-form form .first-wrapper select {
  appearance: none;
  background-image: url('data:image/svg+xml;utf8,<svg fill="%23362796" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M7 10l5 5 5-5z"/><path d="M0 0h24v24H0z" fill="none"/></svg>');
  background-repeat: no-repeat;
  background-position: right 10px center;
}

.containerd.add-doctor-form form .first-wrapper button {
  background-color: blue;
  color: #ffffff;
  padding: 12px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.containerd.add-doctor-form form .first-wrapper button:hover {
  background-color: rgb(13, 13, 122); 
}


 
