/* Appointment---------------------------------------------------------------------------*/

.appointment-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #f0f0f0, #ffffff); 
  border-radius: 8px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
  color: blue; 
  padding-left: 80px;
  z-index: 2;
}


.appointment-heading {
  flex: 1 1 50%;
  padding: 20px;
}

.appointment-heading h2 {
  font-size: 2rem;
  margin-bottom: 10px;
}

.appointment-heading p {
  font-size: 1rem;
  line-height: 1.5;
}

.appointment-img {
  flex: 1 1 50%;
  text-align: center;
  z-index: 1;
}

.appointment-img img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  transition: transform 0.3s, filter 0.3s;
  animation: float 3s ease-in-out infinite;
  z-index: 1;
 
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@media (max-width: 768px) {
  .appointment-container {
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
  }

  .appointment-heading,
  .appointment-img {
    flex: 1 1 100%;
    padding: 10px;
  }
}

/* AppointmentForm styling */
.containerApp {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #4e54c8, #8f94fb); 
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
  color: #fff; 
}

.appointmentsign {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: bold;
}

.appointment-form form {
  display: grid;
  gap: 15px;
}

.appointment-form form div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.appointment-form form div:last-child {
  grid-template-columns: 1fr;
}

.appointment-form input[type="text"],
.appointment-form input[type="number"],
.appointment-form input[type="date"],
.appointment-form select,
.appointment-form textarea {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.8); 
  transition: border-color 0.3s ease;
}

.appointment-form input[type="checkbox"] {
  width: auto;
}

.appointment-form input[type="text"]:focus,
.appointment-form input[type="number"]:focus,
.appointment-form input[type="date"]:focus,
.appointment-form select:focus,
.appointment-form textarea:focus {
  outline: none;
  border-color: #fff; 
}

.appointment-form select {
  padding: 10px;
}

.appointment-form textarea {
  resize: vertical;
}

.appointmentbtn {
  background-color: #fff; 
  color: #4e54c8; 
  border: none;
  padding: 12px 24px;
  font-size: 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.appointmentbtn:hover {
  background-color: #8f94fb; 
  color: #fff; 
}


@media (max-width: 768px) {
  .containerApp {
    padding: 10px;
  }

  .appointment-form form div {
    grid-template-columns: 1fr;
  }
}
