
/* -----------------------------Hero Section-------------------------------------- */
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.hero-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 20px;
  /* background: linear-gradient(120deg, #ff9671, #ffc75f); */
  color: blue;
  height: 100vh;
}

.text-section {
  flex: 1;
  max-width: 50%;
}

.text-section h1 {
  font-size: 3em;
  margin-bottom: 20px;
  color: blue;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.text-section p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

.hero-description {
  font-size: 1em;
  line-height: 1.5;
  color: #918e8e;
}

.image-section {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  animation: float 3s ease-in-out infinite;
  transition: transform 0.3s;
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@media (max-width: 768px) {
  .hero-container {
    flex-direction: column;
    height: auto;
    padding: 20px 10px;
  }

  .text-section {
    max-width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }

  .text-section h1 {
    font-size: 2em;
  }

  .text-section p {
    font-size: 1em;
  }

  .hero-description {
    font-size: 0.9em;
  }

  .image-section {
    justify-content: center;
  }

  .hero-image,
  .vector-image {
    max-width: 100%;
  }
}


/* -----------------------------------------Biography section------------------ */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');


body {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f0f2f5;
}

.container-biography {
  max-width: 1000px;
  margin: 60px auto;
  padding: 40px;
  background: linear-gradient(145deg, #00c6ff, #0072ff);
  border-radius: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  color: #fff;
  overflow: hidden;
  position: relative;
  text-align: center;
}

.container-biography::before {
  content: '';
  position: absolute;
  top: -50px;
  left: -50px;
  width: 200px;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: spin 6s infinite linear;
}

.container-biography::after {
  content: '';
  position: absolute;
  bottom: -50px;
  right: -50px;
  width: 200px;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: spin 6s infinite linear reverse;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.sign {
  font-size: 3em;
  font-weight: 700;
  margin-bottom: 40px;
  position: relative;
  z-index: 1;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 5px;
  display: inline-block;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}

.fast-flicker,
.flicker {
  animation: flicker 2s infinite;
}

@keyframes flicker {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.banner1,
.banner2 {
  margin: 20px 0;
  text-align: center;
}

.bioimg {
  width: 100%;
  max-width: 500px;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s, filter 0.3s;
  animation: float 3s ease-in-out infinite;
}

.bioimg:hover {
  transform: scale(1.05);
  filter: brightness(1.2);
}

/* Content Styles */
.banner2 h3 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #fff;
  text-transform: uppercase;
}

.banner2 p {
  font-size: 1.2em;
  line-height: 1.6;
  margin-bottom: 20px;
  color: #e0e0e0;
  text-align: justify;
}

@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .container-biography {
    padding: 20px;
  }

  .sign {
    font-size: 2.5em;
  }

  .banner2 h3 {
    font-size: 2em;
  }

  .banner2 p {
    font-size: 1em;
  }
}

@media (max-width: 480px) {
  .sign {
    font-size: 2em;
  }

  .banner2 h3 {
    font-size: 1.5em;
  }

  .banner2 p {
    font-size: 0.9em;
  }
}

