@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f5f7fa;
}

.container.form-component.message-form {
  max-width: 800px;
  margin: 60px auto;
  padding: 40px;
  background: linear-gradient(135deg, #6a11cb, #2575fc);
  border-radius: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  color: #fff;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.container.form-component.message-form::before {
  content: '';
  position: absolute;
  top: -50px;
  left: -50px;
  width: 200px;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: spin 6s infinite linear;
}

.container.form-component.message-form::after {
  content: '';
  position: absolute;
  bottom: -50px;
  right: -50px;
  width: 200px;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: spin 6s infinite linear reverse;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Sign Styles */
.sign {
  font-size: 2.5em;
  font-weight: 700;
  margin-bottom: 40px;
  position: relative;
  z-index: 1;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 5px;
  display: inline-block;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 10px;
}

.fast-flicker,
.flicker {
  animation: flicker 2s infinite;
}

@keyframes flicker {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

/* Form Styles */
.form-msg {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-msg div {
  display: flex;
  gap: 20px;
}

.form-msg input,
.form-msg textarea {
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  border: none;
  outline: none;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 1em;
  transition: background 0.3s;
}

.form-msg input::placeholder,
.form-msg textarea::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

.form-msg input:focus,
.form-msg textarea:focus {
  background: rgba(255, 255, 255, 0.3);
}

.form-msg textarea {
  resize: none;
}

  button.msgbtn {
    background: linear-gradient(135deg, #ff6f91,  #0072ff); 
    border: none; 
    color: white;
    padding: 15px 30px; 
    text-align: center; 
    text-decoration: none;
    display: inline-block; 
    font-size: 16px; 
    font-weight: bold; 
    margin: 10px 2px; 
    cursor: pointer; 
    border-radius: 8px; 
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
    transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transitions */
    position: relative;
    overflow: hidden;
    animation: glowing 1.5s infinite alternate; 
  }
  /* #00c6ff */
  
  button.msgbtn:hover {
    background-color: #ff9900; 
    transform: scale(1.05); 
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2); 
  }

  @keyframes glowing {
    0% {
      box-shadow: 0 0 5px #ff6f91, 0 0 10px #ff6f91, 0 0 15px #ff6f91;
    }
    100% {
      box-shadow: 0 0 20px #00c6ff, 0 0 30px #00c6ff, 0 0 40px #00c6ff;
    }
  }
  


/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .container.form-component.message-form {
    padding: 20px;
  }

  .sign {
    font-size: 2em;
  }

  form div {
    flex-direction: column;
  }
}


/* Footer Section-------------------------------------------------------------------- */

footer {
  background-color: #f1efef;
  padding: 40px 20px;
  text-align: center;
}

footer hr {
  border: 0;
  height: 1px;
  background: #444;
  margin: 20px 0;
}

footer .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  text-align: left;
}

footer .content div {
  flex: 1;
  margin: 20px;
  min-width: 200px;
}

footer .logo-img {
  max-width: 150px;
  margin-bottom: 20px;
}

footer h4 {
  font-size: 1.2em;
  margin-bottom: 20px;
  color: blue;
}

footer ul {
  list-style: none;
  padding: 0;
}

footer ul li,
footer ul a {
  margin-bottom: 10px;
  color: #a5a4a4;
  text-decoration: none;
  transition: color 0.3s;
}

footer ul li:hover,
footer ul a:hover {
  color: blue;
}

footer ul li span {
  display: block;
}

footer .content div span {
  margin-left: 10px;
}

footer .content div > div {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

footer .content div > div span {
  margin-left: 10px;
  color: #a5a4a4;
}

footer .content div > div svg {
  color: blue;
}

footer .content div > div:hover span {
  color: blue;
}

@media (max-width: 768px) {
  footer .content {
    flex-direction: column;
    text-align: center;
  }

  footer .content div {
    margin: 10px 0;
  }

  footer .logo-img {
    margin: 0 auto 20px;
  }
}

